<div class="c-quick"><div class="c-quick__inner"><div class="c-quick__top"><a v-for="item of list" href="javascript:;" :class="item.class" :title="item.name"><mdicon :name="item.icon" size="15"></mdicon></a></div><div class="c-quick__temperature"><dl><dt>Outdoor Temperature</dt><dd class="type--number"><strong>27</strong> <span>℃</span></dd><dd class="type--weather">Cloudy</dd></dl><a href="javascript:;"><mdicon name="dots-horizontal" size="15"></mdicon></a></div><div class="c-quick__device"><h2>{{ msg }}</h2><a class="btn-more" href="javascript:;"><mdicon name="dots-horizontal" size="15"></mdicon></a><el-scrollbar class="pst--scroll" height="155px"><div class="c-card"><div class="c-card__item" v-for="item of device"><div class="icon"><mdicon :name="item.icon" size="20"></mdicon></div><dl><dt>{{ item.name }}</dt><dd>{{ item. value }}</dd></dl><el-button :class="{'is-active': item.state === 1}" circle><mdicon name="power-standby" size="13"></mdicon></el-button></div></div></el-scrollbar></div></div></div>